.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}


.logo {
  font-family: Avenir, sans-serif;
  font-style: normal;
  font-weight: 900;
  font-size: 18px;
  line-height: 100%;
  color: #2F2F2F;
}

.main-menu {
  &__item {
    display: inline-block;
  }

  &__link {
    text-decoration: none;
  }
}

.user-menu {
  &__item {
    display: inline-block;
  }

  &__link {
    text-decoration: none;
  }
}
